.cat-info {
    margin-top: 20px;
    padding: 20px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 40px;
    background-color: #e5f3f2;
    border: 1px solid #0d161b;
}

.box-img {
    display: flex;
    align-items: center;
}

.box {
    width: 400px;
}